#AllEmpoyeeCards {
     h1 {
          text-transform: capitalize;
          justify-content: center;
          padding: 0.5em;
     }
     p, h1 {
          margin: 0;
          display: flex;
          svg {
               margin-right: 0.5em;
          }
     }
     .info-div {
          padding: 0.5em 1em;
     }
     input {
          width: 100%;
          border: none;
          background: transparent;
          outline: none;
          pointer-events: none;
     }
     .img-wrapper {
          svg {
               font-size: 9em;
               fill: var(--m-grey);
          }
     }
     .icon-div {
          display: flex;
          justify-content: space-between;
          margin: .5em;
          svg {
               font-size: var(--icon-size);
               fill: var(--dark-grey);
               &:hover {
                    fill: var(--pink)
               }
          }
     }
}
